import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOmniSignatoriesResponse, IOptionsPsSignatoryCardExposed } from 'src/app/commonBussinessSRC/psComponents/ps-signatory-card/ps-signatory-card.component.interface';
import { IOptionsPsComplexSegment } from 'src/app/commonSRC/psServices/models/ps-common-interface';
